@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,700;1,300;1,400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cookie&family=Playfair+Display&family=Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body {
  font-family: 'Roboto', sans-serif;
  box-sizing: border-box;
  margin: 0;
}

main {
  margin: 70px 100px;
  display: flex;
  justify-content: space-between;
}

a {
  text-decoration: none;
  color: black;
}

.left {
  margin-right: 50px;
}

.search {
  width: 350px;
  border: 1px solid #C4C4C4;
  padding: 6px 16px;
}

.quote-list {
  list-style: none;
  padding: 0;
  height: 400px;
  overflow-y: scroll;
}

footer {
  text-align: center;
}


/*RESPONSIVE*/

@media (max-width: 1024px) {
  main {
    flex-direction: column;
    margin: 70px 40px;
  }

  .search {
    width: 82%;
  }
}

.logo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 10px 0;
}

.feather {
  margin-bottom: 12px;
  height: 30px;
  width: auto;
}

.say {
  font-weight: bold;
  font-size: 48px;
  margin: 0;
}

.quote-bar {
  font-weight: lighter;
  font-size: 16px;
  margin: 0;
}

.quote-generator {
  display: flex;
  flex-direction: column;
}

/*QUOTE CARD BUILD*/

.quote-card-build {
  padding: 10% 10%;
  width: 445px;
}

.selected-quote-author {
  text-align: right;
  font-style: italic;
  margin-top: 24px;
}


/*DOWNLOAD BUTTON*/
.download-button {
  background: black;
  width: 150px;
  color: white;
  padding: 12px 14px;
  border: none;
  margin: 30px 0 25px 0;
  align-self: flex-end;
  font-family: inherit;
  font-weight: lighter;
}

.download-button:hover {
  background: #686868;
  cursor: pointer;
}


/*STYLING OPTIONS*/

/*Color*/

.color-options,
.font-options {
  display: flex;
  margin-bottom: 28px;
}

.choose-color>p,
.choose-font>p {
  font-weight: lighter;
}

.color-dot {
  height: 32px;
  width: 32px;
  border-radius: 50%;
  margin-right: 12px;
  cursor: pointer;
}

.color-dot.green {
  background: #69CCAE;
}

.color-dot.pink {
  background: #EB8BB4;
}

.color-dot.yellow {
  background: #F3C958;
}

.color-dot.coral {
  background: #F68874;
}

.color-dot.grey {
  background: #BAD4D4;
}


/*Font*/

.font {
  border: 1px solid black;
  margin: 0 12px 0 0;
  padding: 6px 10px;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.cookie {
  font-family: 'Cookie', cursive;
  font-size: 24px;
}

.playfair-display {
  font-family: 'Playfair Display', serif;
}

.raleway {
  font-family: 'Raleway', sans-serif;
}



/*RESPONSIVE*/

@media (max-width: 1024px) {
  .quote-generator{
    width: 100%;
  }

  .quote-card-build {
    width: 80%;
  }

  .download-button {
    align-self: flex-start;
  }
}

.quote-card {
  width: 350px;
  border: 1px solid black;
  padding: 3px 19px;
  margin: 0 16px 20px 0;
  cursor: pointer;
}

.selected {
  border: 4px solid black;
  padding: 0 16px;
  cursor: auto;
}

.author {
  text-align: right;
  font-style: italic;
}


/*RESPONSIVE*/

@media (max-width: 1024px) {
  .quote-card {
    width: 80%;
  }
}

